
import { makeStyles } from '@material-ui/core';
import { Container, Grid,
         Typography, 
      } from '@material-ui/core';
import React, { useEffect, useState } from 'react';
import { LineChart, Line, XAxis, YAxis, 
          CartesianGrid, Tooltip, Legend, 
          ResponsiveContainer 
        } from 'recharts';

/* Styles for the PuzzleChart component  */

const useStyles = makeStyles( (theme) => ({

  root: {
    flexGrow: 1,
  },
  page: {
    fontFamily: "'Segoe UI', 'Roboto', 'Arial', 'Helvetica', sans-serif",
    textAlign: 'center',
    padding: '0',  
  },
  subheader: {
    fontFamily: "'Segoe UI', sans-serif",
    fontWeight: 'bold',
    marginTop: theme.spacing(1),
    marginBottom: theme.spacing(1), 
  },
  body: {
    padding: theme.spacing(2),
    marginTop: theme.spacing(0),
    marginBottom: theme.spacing(0),
  },
}));


function PuzzleChart(props) {

  const classes = useStyles();

  const logToChart = props.puzzleLog.filter((puzzleLog) => {
    return (puzzleLog.id === props.statsId);
  })[0];

  const [data, setData] = useState();

  useEffect( () => {

    console.log(logToChart);
    if(logToChart != null){
      setData(logToChart.chartData);
    }
  },[logToChart])

  return (

    <Container maxWidth='md' className={classes.page}>
      <div className={classes.root}>

        {(logToChart==null)
          ? null
          : <>
            <Container style={{width:'100%', marginBottom: '10px',}}>
                <Grid container spacing={1}>

                <Grid item xs={12}>
                    <Typography
                    variant='h6'
                    component='h2'
                    className={classes.subheader}
                    >Puzzle progression (pieces/time)
                    </Typography>
                </Grid>

                <ResponsiveContainer width='100%' aspect={1.25}>
                  <LineChart
                    //width={500}
                    //height={300}
                    data={data}
                    margin={{ top: 5, right: 5, left: 5, bottom: 5 }}
                  >
                    <CartesianGrid strokeDasharray='3 3' />
                    <XAxis 
                      dataKey='time'
                      /* type='number'
                      ticks={[10,20,30,[...],100]}
                      domain={[10,100]} */
                    />
                    <YAxis />
                    <Tooltip />
                    <Legend />
                    <Line type ='monotone' dataKey="pieces" stroke='darkslateblue' activeDot={{ r:8 }} />
                  </LineChart>
                </ResponsiveContainer>

                </Grid>
            </Container>
          </>
        }

      </div>
    </Container>
  );
}

export default PuzzleChart;
