import { makeStyles } from '@material-ui/core';
import { IconButton, ListItem, List,
          ListItemIcon, ListItemText, 
          Grid, Container, Divider, 
          Typography, Button, 
        } from "@material-ui/core";
import ExtensionIcon from '@material-ui/icons/Extension';
import HighlightOffIcon from '@material-ui/icons/HighlightOff';
import EditIcon from '@material-ui/icons/Edit';
import ScheduleIcon from '@material-ui/icons/Schedule';
import PeopleAltIcon from '@material-ui/icons/PeopleAlt';
import HourglassEmptyIcon from '@material-ui/icons/HourglassEmpty';
import HourglassFullIcon from '@material-ui/icons/HourglassFull';

import { Link } from 'react-router-dom';
import { format } from 'date-fns';
import StarRating from './StarRating';

/* Styles for the Chart component  */
const useStyles = makeStyles( (theme) => ({

  root: {
    flexGrow: 1,
  },
  page: {
    fontFamily: "'Segoe UI', 'Roboto', 'Arial', 'Helvetica', sans-serif",
    textAlign: 'center',
    padding: '0',  
    maxWidth: '480px',
  },
  body: {
    padding: theme.spacing(2),
    marginTop: theme.spacing(0),
    marginBottom: theme.spacing(0),
  },
  iconSmall: {
      color: 'darkslateblue',
      marginRight: theme.spacing(1),
      marginLeft: theme.spacing(2),
      marginTop: theme.spacing(1),
      height: '16px',
  },
  iconSmallFirst: {
    color: 'darkslateblue',
    marginRight: theme.spacing(1),
    marginLeft: theme.spacing(0),
    marginTop: theme.spacing(1),
    height: '16px',
},
  list: {
    width: '100%',
    marginTop: theme.spacing(1),
    marginBottom: theme.spacing(2),
},
  listItem: {
    border: '2px',
  },
}));

const getTimeDifference = (StartDate, EndDate) => {

  let milliseconds = Math.abs(EndDate - StartDate);
  let seconds = Math.floor(milliseconds / 1000);
  let minutes = Math.floor(seconds / 60);
  let hours = Math.floor(minutes / 60);

  seconds = seconds % 60;
  minutes = minutes % 60;
  hours = hours % 24;

  return (hours + ' h ' + minutes + ' min ' + seconds + ' s');
}

function PuzzleList(props) {

  const classes = useStyles();

  const text = {
    fontWeight: 'bold',
    marginBottom: '15px',
  }

  const smalltext = {
    fontSize: '14px',
    marginRight: '10px',
    //minWidth: '40px',
  }

  return (

    <Container maxWidth='md' className={classes.page}>
      <div className={classes.root}>

        <Container style={{width:'100%', padding: '0px',}}>
          <Grid container spacing={1}>
            <Grid item xs={12}>

              <List className={classes.list} disablePadding>
                <Divider />

                  {props.puzzleLog.map( (puzzleLog, idx) => {

                    return (

                      <ListItem 
                          key={idx} 
                          className={classes.ListItem}
                          divider={true}
                      >

                        <ListItemText 
                            primaryTypographyProps={{ style: text }}
                            secondaryTypographyProps={{ style: smalltext }}
                            primary={puzzleLog.puzzleName}
                            secondary={<>                                
                                <StarRating stars={puzzleLog.stars} /> 
                                
                                <PeopleAltIcon className={classes.iconSmallFirst} />
                                {puzzleLog.puzzleMaker} 
                                <ExtensionIcon className={classes.iconSmall} />
                                {/* {(puzzleLog.chartData.pieces) ? <>{puzzleLog.chartData[(puzzleLog.chartData.length-1)].pieces} / {puzzleLog.maxPieces}</> : null } */}
                                {puzzleLog.chartData[(puzzleLog.chartData.length - 1)].pieces} / {puzzleLog.maxPieces}
                                <br/>

                                <HourglassEmptyIcon className={classes.iconSmallFirst} />
                                <span style={{fontWeight:'bold'}}>{format(puzzleLog.logStartDate, 'HH:mm')}</span> {format(puzzleLog.logStartDate, 'dd.MM.y')} 
                                <HourglassFullIcon className={classes.iconSmall} />
                                <span style={{fontWeight:'bold'}}>{format(puzzleLog.logEndDate, 'HH:mm')}</span>  {format(puzzleLog.logEndDate, 'dd.MM.y')} 
                                <br/>
                                
                                <PeopleAltIcon className={classes.iconSmallFirst} />
                                {puzzleLog.puzzleMode} 
                                <ScheduleIcon className={classes.iconSmall} />
                                {getTimeDifference(puzzleLog.logStartDate, puzzleLog.logEndDate)}
                                <br/>

                                <IconButton 
                                  edge='end'
                                  style={{marginLeft: '200px'}}
                                  component={Link}
                                  to={`/edit/${puzzleLog.id}`}
                                >
                                  <EditIcon />
                                </IconButton>

                                <IconButton 
                                  edge='end'
                                  style={{ marginLeft: '15px'}}
                                  component={Link}
                                  to={`/delete/${puzzleLog.id}`}
                                >
                                  <HighlightOffIcon />
                                </IconButton>

                            </>}
                        />
                      </ListItem>
                    );
                  })}
                </List>
            </Grid>
          </Grid>
        </Container>

      </div>
    </Container>
  );
}

export default PuzzleList;
