
import { useState, useEffect } from 'react';
import { makeStyles } from '@material-ui/core';
import { Container, 
      } from '@material-ui/core';
import TimerButtons from './TimerButtons';
import Timer from './Timer';

/* Styles for the Stopwatch component  */

const useStyles = makeStyles( (theme) => ({

  root: {
    flexGrow: 1,
  },
  page: {
    fontFamily: "'Segoe UI', 'Roboto', 'Arial', 'Helvetica', sans-serif",
    textAlign: 'center',
    padding: '0',  
  },
  body: {
    padding: theme.spacing(2),
    marginTop: theme.spacing(0),
    marginBottom: theme.spacing(0),
  },
  reset: {
      textAlign: 'right',
      marginBottom: '0',
  },
}));


function Stopwatch(props) {

  const classes = useStyles();

  const [isActive, setIsActive] = useState(false);
  const [isPaused, setIsPaused] = useState(true);
  const [isReset, setIsReset] = useState(false);
  const [startOk, setStartOk] = useState(false);

  const [time, setTime] = useState(0);

  const [open, setOpen] = useState(false);

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  }


  useEffect( () => {
    
    let interval = null;

    if (isActive && isPaused === false) {
    
      interval = setInterval( () => {
        setTime( (time) => time + 10);
      }, 10);

    } else {
      clearInterval(interval);
    }
    return () => {
      clearInterval(interval);
    };

  }, [isActive, isPaused]);

  const handleStart = () => {
    setIsActive(true);
    setIsPaused(false);
    setIsReset(false);
  };

  const handleStartOk = () => {
    setStartOk(true);
  }

  const handlePauseResume = () => {
    setIsPaused(!isPaused);
  };

  const handleReset = () => {
    setIsActive(false);
    setTime(0);
    setIsReset(true);
    setStartOk(false);
/*     props.handleGetReset(true); */
  };

  useEffect( () => {
    props.handleGetPaused(isPaused);
  }, [isPaused]);

  useEffect( () => {
    props.handleGetReset(isReset);
  }, [isReset]);

  useEffect( () => {
    console.log(startOk);
    if (startOk && !isActive) {
      handleStart();
    }
  }, [startOk, isActive]);

  return (

    <Container maxWidth='md' className={classes.page}>

      <div className={classes.root}>

        <Timer 
          time={time}
        />

        <TimerButtons 
          isActive={isActive}
          isPaused={isPaused}
          handleStartOk={handleStartOk}
          handlePauseResume={handlePauseResume}
          handleReset={handleReset}
          handleGetMaxInput={props.handleGetMaxInput}
          open={open}
          handleClickOpen={handleClickOpen}
          handleClose={handleClose}
          startOk={startOk}
          setStartOk={setStartOk}
          setOpen={setOpen}
          puzzleLog={props.puzzleLog}
          setPuzzleLog={props.setPuzzleLog} 
        />

      </div>
    </Container>
  );
}

export default Stopwatch;
