import { makeStyles } from '@material-ui/core';
import { Container, Grid,
         Typography,
      } from '@material-ui/core';
import ShowOnline from './ShowOnline';
import React, { useState, useEffect } from 'react';
import api from './api';

/* Styles for Twitch page */

const useStyles = makeStyles( (theme) => ({

  root: {
    flexGrow: 1,
  },
  page: {
    fontFamily: "'Segoe UI', 'Roboto', 'Arial', 'Helvetica', sans-serif",
    textAlign: 'center',
    padding: '0',  
  },
  header: {
    fontFamily: "'Segoe UI', sans-serif",
    fontWeight: 'bold',
    color: 'darkslateblue', 
    marginTop: theme.spacing(2),
    marginBottom: theme.spacing(0),
    padding: theme.spacing(2), 
  },
  body: {
    padding: theme.spacing(2),
    marginTop: theme.spacing(0),
    marginBottom: theme.spacing(0),
  },
}));


function Twitch(props) {

  const classes = useStyles();

  const [streams, setStreams] = useState([]);
  const [refresh, setRefresh] = useState(false);

  const handleRefresh = () => {
    setRefresh(!refresh);
  }

  /* Otetaan yhteys Twitch API:hin ja
  noudetaan kaikki tll hetkell online olevat 
  jigsaw puzzle striimit */
  
  useEffect(() => {
    const fetchData = async () => {
      const result = await api.get('https://api.twitch.tv/helix/streams?game_id=7331');
      console.log(result.data);
      setStreams(result.data.data);
    }
    fetchData();
  }, [refresh])


  return (

    <Container maxWidth='md' className={classes.page}>

      <div className={classes.root}>

        <Grid container spacing={1}>

          <Grid item xs={12}>
            <Typography
              variant='h4'
              component='h1'
              className={classes.header}
            >Online on Twitch
            </Typography>
          </Grid>

        </Grid>

        <ShowOnline 
          streams={streams}
          handleRefresh={handleRefresh}
        />

      </div>
    </Container>
  );
}

export default Twitch;