import { useState, useRef } from "react";
import { makeStyles } from '@material-ui/core';
import { TextField, Button, Grid, 
        Container, Typography, Select, 
        FormControl, InputLabel 
    } from "@material-ui/core";
import { Link, useHistory, useParams } from 'react-router-dom';
import { MuiPickersUtilsProvider } from "@material-ui/pickers";
import DateFnsUtils from '@date-io/date-fns';
import { fi } from 'date-fns/locale';

/* Styles for Edit page  */

const useStyles = makeStyles( (theme) => ({

    root: {
      flexGrow: 1,
    },
    page: {
      fontFamily: "'Segoe UI', 'Roboto', 'Arial', 'Helvetica', sans-serif",
      textAlign: 'center',
      padding: '0',  
    },
    subheader: {
      fontFamily: "'Segoe UI', sans-serif",
      fontWeight: 'bold',
      marginTop: theme.spacing(5),
      marginBottom: theme.spacing(5), 
    },
    input: {
        width: '100%',
        marginTop: theme.spacing(1),
        marginBottom: theme.spacing(3),
        textAlign: 'left',
    },
    date: {
        width: '100%',
        marginTop: theme.spacing(0),
        marginBottom: theme.spacing(2),
    },
    button: {
        width: '100%',
        marginTop: theme.spacing(1),
        marginBottom: theme.spacing(1),
        backgroundColor: 'darkslateblue',
        borderRadius: '10px',
        height: '50px',
    },
    buttonCancel: {
        width: '100%',
        marginTop: theme.spacing(1),
        marginBottom: theme.spacing(1),
        borderRadius: '10px',
        height: '50px',
    },
  }));
  

function Edit(props) {

    const classes = useStyles();
    const history = useHistory();
    const { id } = useParams();

    const logToEdit = props.puzzleLog.filter((puzzleLog) => {
                            return (puzzleLog.id === id);
                        })[0];
    
    const puzzleNameField = useRef();
    const puzzleMakerField = useRef();
    const maxPiecesField = useRef();
    const puzzleModeField = useRef();
    const starField = useRef();

    const [errorMsg, setErrorMsg] = useState({});
  
    const editLog = (e) => {
  
      e.preventDefault();
  
      let errors = {};
  
      if (!puzzleNameField.current.value) {
        errors = {...errors, puzzleName: 'Write the name of the puzzle'}
      }
  
      if (!maxPiecesField.current.value) {
          errors = {...errors, maxPieces: 'Enter max pieces'}
        }
  
      if (!puzzleModeField.current.value) {
        errors = {...errors, puzzleMode: 'Choose puzzle mode'}
      }
  
      if (Object.entries(errors).length > 0) {
  
        setErrorMsg({...errors});
      
      } else {
        console.log('Form filled with no errors');
        setErrorMsg({});

        let editedLog = {
          id: id,
          puzzleName: puzzleNameField.current.value,
          maxPieces: maxPiecesField.current.value,
          puzzleMaker: puzzleMakerField.current.value,
          puzzleMode: puzzleModeField.current.value,
          logStartDate: logToEdit.logStartDate,
          logEndDate: logToEdit.logEndDate,
          totalTime: logToEdit.totalTime,
          stars: starField.current.value,
          chartData: logToEdit.chartData,
        };
  
        let oldPuzzleLog = props.puzzleLog.filter( (puzzleLog) => {
            return(puzzleLog.id !== id);
        });

        // Luodaan apuarray vanhasta listasta + lisyksest ja lajitellaan se uusimmasta vanhimpaan
        let editedLogPush = [...oldPuzzleLog, editedLog].sort((a, b) => b.logEndDate - a.logEndDate);
        console.log(editedLogPush);
  
        // Listn lajiteltu apuarray uudeksi listaksi
        props.setPuzzleLog(editedLogPush);
  
        history.push('/log');
    }
}

    return (

        <Container maxWidth='md' className={classes.page}>
        <div className={classes.root}>

            <Typography
              variant='h5'
              component='h1'
              className={classes.subheader}
            >Editing puzzle log for <br/> 
            <span style={{ color: 'darkslateblue' }}> 
                {logToEdit.puzzleName}
            </span>
            </Typography>

            <MuiPickersUtilsProvider utils={DateFnsUtils} locale={fi}>
            <form onSubmit={editLog}>

            <Container style={{ width:'100%' }}>
            <Grid container spacing={1}>

                <Container style={{ width:'100%' }}>
                <Grid container spacing={1}>

                    <Grid item xs={12}>
                    <TextField
                        name='puzzleName'
                        variant='outlined'
                        label='Puzzle name *'
                        defaultValue={logToEdit.puzzleName}
                        InputLabelProps={{ shrink: true }}
                        inputRef={puzzleNameField}
                        className={classes.input}
                        error={Boolean(errorMsg.puzzleName)}
                        helperText={errorMsg.puzzleName}
                    />
                    </Grid>

                </Grid>
                </Container>

                <Container style={{ width:'100%' }}>
                <Grid container spacing={1}>

                    <Grid item xs={8}>
                    <TextField
                        name='puzzleMaker'
                        variant='outlined'
                        label='Puzzle maker'
                        defaultValue={logToEdit.puzzleMaker}
                        InputLabelProps={{ shrink: true }}
                        inputRef={puzzleMakerField}
                        className={classes.input}
                    />
                    </Grid>

                    <Grid item xs={4}>
                    <TextField
                        name='maxPieces'
                        variant='outlined'
                        label='Max pieces *'
                        defaultValue={logToEdit.maxPieces}
                        InputLabelProps={{ shrink: true }}
                        inputRef={maxPiecesField}
                        className={classes.input}
                        error={Boolean(errorMsg.maxPieces)}
                        helperText={errorMsg.maxPieces}
                    />
                    </Grid>

                </Grid>
                </Container>

                <Container style={{ width:'100%' }}>
                <Grid container spacing={1}>

                    <Grid item xs={8}>

                    <FormControl
                        variant='outlined'
                        className={classes.input}
                    >
                    <InputLabel
                        shrink={true}
                    >Puzzle mode *</InputLabel>
                    <Select
                        name='puzzleMode'
                        variant='outlined'
                        label='Puzzle mode *'
                        defaultValue={logToEdit.puzzleMode}
                        InputLabelProps={{ shrink: true }}
                        inputRef={puzzleModeField}
                        className={classes.input}
                        error={Boolean(errorMsg.puzzleMode)}
                        helperText={errorMsg.puzzleMode}
                    >
                        <option value='' disabled>Choose puzzle mode</option>
                        <option value='Solo casual'>Solo casual</option>
                        <option value='Solo competitive'>Solo competitive</option>
                        <option value='2-4 people casual'>2-4 people casual</option>
                        <option value='2-4 people competitive'>2-4 people competitive</option>
                    </Select>
                    </FormControl>

                    </Grid>

                    <Grid item xs={4}>
                        <FormControl
                            variant='outlined'
                            className={classes.input}
                        >
                            <InputLabel>Stars</InputLabel>
                            <Select
                                name='stars'
                                variant='outlined'
                                label='Star rating'
                                defaultValue={logToEdit.stars}
                                InputLabelProps={{ shrink: true }}
                                inputRef={starField}
                                className={classes.input}
                        >
                            <option value={0}>0</option>
                            <option value={1}>1</option>
                            <option value={2}>2</option>
                            <option value={3}>3</option>
                            <option value={4}>4</option>
                            <option value={5}>5</option>
                        </Select>
                        </FormControl>
                    </Grid>


                </Grid>
                </Container>

            </Grid>
            </Container>

            <Container style={{ width: '85%'}}>
                <Grid container spacing={2}>

                    <Grid item xs={6}>
                        <Button
                            variant='contained'
                            color='default'
                            className={classes.buttonCancel}
                            component={Link}
                            to='/log'
                            /* exact='true' */
                            >Cancel
                        </Button>
                    </Grid>

                    <Grid item xs={6}>
                        <Button
                            type='submit'
                            variant='contained'
                            color='primary'
                            className={classes.button}
                        >Save changes
                        </Button>
                    </Grid>
                        
                </Grid>
            </Container>

            </form>
            </MuiPickersUtilsProvider>
            
        </div>
        </Container>
    );
}

export default Edit;
