import { useState, useEffect } from 'react';
import { makeStyles } from '@material-ui/core';
import { Container } from '@material-ui/core';
import Stopwatch from './Stopwatch';
import PieceCounter from './PieceCounter';

/* Styles for the Counter page */

const useStyles = makeStyles( (theme) => ({

  root: {
    flexGrow: 1,
  },
  page: {
    fontFamily: "'Segoe UI', 'Roboto', 'Arial', 'Helvetica', sans-serif",
    textAlign: 'center',
    padding: '0',  
  },
  body: {
    padding: theme.spacing(2),
    marginTop: theme.spacing(0),
    marginBottom: theme.spacing(0),
  },
}));


function Counter(props) {

  const classes = useStyles();

  const [getPaused, setGetPaused] = useState('');
  const handleGetPaused = (childdata) => {
    setGetPaused(childdata);
  }

  const [getReset, setGetReset] = useState(false);
  const handleGetReset = (childdata) => {
    setGetReset(childdata);
  }

  const [getMaxInput, setGetMaxInput] = useState(0);
  const handleGetMaxInput = (childdata) => {
    setGetMaxInput(childdata);
  }
  
  return (

    <Container maxWidth='md' className={classes.page}>

      <div className={classes.root}>

        <Stopwatch 
          handleGetPaused={handleGetPaused}
          handleGetReset={handleGetReset}
          handleGetMaxInput={handleGetMaxInput}
          puzzleLog={props.puzzleLog}
          setPuzzleLog={props.setPuzzleLog} 
        />

        <PieceCounter 
          getPaused={getPaused}
          getReset={getReset}
          getMaxInput={getMaxInput}
          puzzleLog={props.puzzleLog}
          setPuzzleLog={props.setPuzzleLog}
        />

      </div>
    </Container>
  );
}

export default Counter;