import { useState, useEffect } from 'react';
import { BrowserRouter as Router, Route } from 'react-router-dom';
import { v4 as uuid } from 'uuid';
import Home from './components/Home';
import Counter from './components/Counter/Counter';
import StartLog from './components/Counter/StartLog';
import Stats from './components/Stats/Stats';
import Log from './components/Log/Log';
import Edit from './components/Log/Edit';
import Delete from './components/Log/Delete';
import Twitch from './components/Twitch/Twitch';
import Menu from './components/Menu';
import Nav from './components/Nav';


function App() {

  const [puzzleLog, setPuzzleLog] = useState([
    {
      id: uuid(),
      puzzleName: 'Birth of Venus',
      maxPieces: 1000,
      puzzleMaker: 'Trefl',
      puzzleMode: 'Solo casual',
      logStartDate: new Date(2022, 3, 29, 14, 0, 0).getTime(),
      logEndDate: new Date(2022, 3, 29, 22, 39, 0).getTime(),
      totalTime: 31140000,
      stars: 5,
      chartData: [  
                    {time: new Date(2022, 3, 29, 14, 0, 0).getTime(),
                    pieces: 0,},
                    {time: new Date(2022, 3, 29, 15, 59, 0).getTime(),
                    pieces: 140,},
                    {time: new Date(2022, 3, 29, 17, 48, 0).getTime(),
                    pieces: 319,},
                    {time: new Date(2022, 3, 29, 19, 32, 0).getTime(),
                    pieces: 540,},
                    {time: new Date(2022, 3, 29, 21, 17, 0).getTime(),
                    pieces: 632,},
                    {time: new Date(2022, 3, 29, 22, 39, 0).getTime(),
                    pieces: 1000,},
                  ],
    },
    {
      id: uuid(),
      puzzleName: "Exit Puzzle: Witch's Kitchen",
      maxPieces: 759,
      puzzleMaker: 'Ravensburger',
      puzzleMode: '2-4 people casual',
      logStartDate: new Date(2021, 11, 15, 20, 5, 0).getTime(),
      logEndDate: new Date(2021, 11, 16, 3, 39, 0).getTime(),
      totalTime: 27240000,
      stars: 3,
      chartData: [  
                    {time: new Date(2022, 11, 15, 20, 5, 0).getTime(),
                    pieces: 0,},
                    {time: new Date(2022, 11, 15, 23, 41, 0).getTime(),
                    pieces: 219,},
                    {time: new Date(2022, 11, 16, 0, 21, 0).getTime(),
                    pieces: 319,},
                    {time: new Date(2022, 11, 16, 2, 17, 0).getTime(),
                    pieces: 540,},
                    {time: new Date(2022, 11, 16, 3, 8, 0).getTime(),
                    pieces: 628,},
                    {time: new Date(2022, 11, 16, 3, 39, 0).getTime(),
                    pieces: 759,},
                  ],
    },
    {
      id: uuid(),
      puzzleName: 'Sammon puolustus',
      maxPieces: 1200,
      puzzleMaker: 'Tactic',
      puzzleMode: 'Solo competitive',
      logStartDate: new Date(2022, 4, 3, 16, 0, 0).getTime(),
      logEndDate: new Date(2022, 4, 3, 22, 17, 0).getTime(),
      totalTime: 22620000,
      stars: 4,
      chartData: [  
                    {time: new Date(2022, 4, 3, 16, 0, 0).getTime(),
                    pieces: 0,},
                    {time: new Date(2022, 4, 3, 17, 12, 0).getTime(),
                    pieces: 120,},
                    {time: new Date(2022, 4, 3, 18, 46, 0).getTime(),
                    pieces: 307,},
                    {time: new Date(2022, 4, 3, 20, 4, 0).getTime(),
                    pieces: 549,},
                    {time: new Date(2022, 4, 3, 21, 3, 0).getTime(),
                    pieces: 830,},  
                    {time: new Date(2022, 4, 3, 22, 17, 0).getTime(),
                    pieces: 1200,},
                  ],
    },
  ]);

  console.log(puzzleLog);

  const savePuzzleLog = () => {

    console.log('Saving puzzle log...');
    localStorage.setItem('puzzleLog', JSON.stringify(puzzleLog));
  }

  const openPuzzleLog = () => {
    console.log('Retrieving puzzle logs...');

    if (localStorage.getItem('puzzleLog')) {

      console.log('Puzzle log found in local storage, opening log');
      // setPuzzleLog
      setPuzzleLog(JSON.parse(localStorage.getItem('puzzleLog')));

    } else {

      console.log('No puzzle log in local storage, creating new log');
      setPuzzleLog((puzzleLog)); 
      // Jos tyhjenee kokonaan, setPuzzleLog(puzzleLog); ja autotallennus pois
    }

  }

  // Get puzzle log from local storage when app is launched
  useEffect( () => {
    openPuzzleLog();
  }, []);

  // Save puzzle log automatically to local storage whenever log is changed
  useEffect( () => {
    savePuzzleLog();
  }, [puzzleLog]);


  return (

    <Router>

      <Menu />

      <Route path="/" exact={true} component={Home} >
        <Home />
      </Route>

      <Route path="/counter" exact={true} component={Counter} >
        <Counter puzzleLog={puzzleLog} 
                setPuzzleLog={setPuzzleLog} 
        />
      </Route>

      <Route path="/counter/:id" component={Counter} >
        <Counter  puzzleLog={puzzleLog} 
                  setPuzzleLog={setPuzzleLog} 
        />
      </Route>

      <Route path="/stats" component={Stats} >
        <Stats  puzzleLog={puzzleLog} 
                setPuzzleLog={setPuzzleLog}
        />
      </Route>

      <Route path="/log" component={Log} >
        <Log  puzzleLog={puzzleLog} 
              setPuzzleLog={setPuzzleLog}
        />
      </Route>

      <Route path="/startlog" component={StartLog} >
        <StartLog puzzleLog={puzzleLog} 
              setPuzzleLog={setPuzzleLog}
        />
      </Route>

      <Route path="/edit/:id" component={Edit} >
        <Edit  puzzleLog={puzzleLog} 
              setPuzzleLog={setPuzzleLog}
        />
      </Route>

      <Route path="/delete/:id" component={Delete} >
        <Delete  puzzleLog={puzzleLog} 
                setPuzzleLog={setPuzzleLog}
        /> 
      </Route>

      <Route path="/twitch" component={Twitch} >
        <Twitch />
      </Route>

      <Nav />

    </Router>

  );
}

export default App;
