
import { useState, useEffect } from 'react';
import { makeStyles } from '@material-ui/core';
import { Container, Grid,
         Typography, 
      } from '@material-ui/core';

/* Styles for the PuzzleStats component */

const useStyles = makeStyles( (theme) => ({

  root: {
    flexGrow: 1,
  },
  page: {
    fontFamily: "'Segoe UI', 'Roboto', 'Arial', 'Helvetica', sans-serif",
    textAlign: 'center',
    padding: '0',  
  },
  subheader: {
    fontFamily: "'Segoe UI', sans-serif",
    fontWeight: 'bold',
    marginTop: theme.spacing(2),
    marginBottom: theme.spacing(1), 
  },
  statTitle: {
    fontFamily: "'Segoe UI', sans-serif",
    fontWeight: 'bold',
    marginTop: theme.spacing(1),
    marginBottom: theme.spacing(1), 
    textAlign: 'right',
  },
  statContent: {
    fontFamily: "'Segoe UI', sans-serif",
    marginTop: theme.spacing(1),
    marginBottom: theme.spacing(1), 
    textAlign: 'left',
  },
  body: {
    padding: theme.spacing(2),
    marginTop: theme.spacing(0),
    marginBottom: theme.spacing(0),
  },
}));


function PuzzleStats(props) {

  const classes = useStyles();

  const logToView = props.puzzleLog.filter((puzzleLog) => {
    return (puzzleLog.id === props.statsId);
  })[0];

  const [dataToChart, setDataToChart] = useState([]);

  useEffect( () => {
/*     setDataToChart(logToView.chartData); */
    console.log(dataToChart);
  }, [props.statsId])


  const getTimeDifference = (StartDate, EndDate) => {

    let milliseconds = Math.abs(EndDate - StartDate);
    let seconds = Math.floor(milliseconds / 1000);
    let minutes = Math.floor(seconds / 60);
    let hours = Math.floor(minutes / 60);
  
    seconds = seconds % 60;
    minutes = minutes % 60;
    hours = hours % 24;
  
    return (hours + ' h ' + minutes + ' min ' + seconds + ' s');
  }
 

  return (

    <Container maxWidth='md' className={classes.page}>
      <div className={classes.root}>
        
        {(logToView==null)
          ? null
          : <>
            <Container style={{width:'100%'}}>
                <Grid container spacing={1}>

                    <Grid item xs={12}>
                      <Typography
                        variant='h6'
                        component='h2'
                        className={classes.subheader}
                      >Stats for <span style={{color:'darkslateblue'}}>
                          {logToView.puzzleName}</span>:
                      </Typography>
                    </Grid>

                </Grid>
            </Container>

            <Container style={{width:'100%'}}>
              <Grid container spacing={1}>

                <Grid item xs={5}>
                  <Typography
                    className={classes.statTitle}
                  >
                    Puzzle name:<br/>
                    Puzzle maker:<br/>
                    Max. pieces:<br/>
                    Total time:<br/>
                  </Typography>
                </Grid>

                <Grid item xs={7}>
                  {(logToView==null)
                    ? null
                    : <>
                        <Typography
                          className={classes.statContent}
                        >
                          {logToView.puzzleName}<br/>
                          {logToView.puzzleMaker}<br/>
                          {logToView.maxPieces}<br/>
                          {getTimeDifference(logToView.logStartDate, logToView.logEndDate)}<br/>
                        </Typography>
                      </>
                  }
                </Grid>

              </Grid>
            </Container>
          </>
        }

      </div>
    </Container>
  );
}

export default PuzzleStats;
