import { useState, useRef } from "react";
import { TextField, Button, 
          Grid, Container, Typography,
          Dialog, DialogActions, 
          DialogContent, DialogTitle, 
          FormControl, Select, InputLabel,
        } from "@material-ui/core";
import { makeStyles } from '@material-ui/core';
import { useHistory } from 'react-router-dom';
import { MuiPickersUtilsProvider } from "@material-ui/pickers";
import DateFnsUtils from '@date-io/date-fns';
import { fi } from 'date-fns/locale';
import { v4 as uuid } from 'uuid';

/* Styles for the StartLog component */

const useStyles = makeStyles( (theme) => ({

  root: {
    flexGrow: 1,
  },
  page: {
    fontFamily: "'Segoe UI', 'Roboto', 'Arial', 'Helvetica', sans-serif",
    textAlign: 'center',
    padding: '0',  
  },
  header: {
    fontFamily: "'Segoe UI', sans-serif",
    fontWeight: 'bold',
    color: 'darkslateblue', 
    marginTop: theme.spacing(1),
    marginBottom: theme.spacing(4),
  },
  input: {
      width: '100%',
      marginTop: theme.spacing(0),
      marginBottom: theme.spacing(2),
  },
  button: {
    width: '100%',
    marginTop: theme.spacing(2),
    marginBottom: theme.spacing(1),
    backgroundColor: 'darkslateblue',
    borderRadius: '10px',
    height: '50px',
  },
  buttonCancel: {
    width: '100%',
    marginTop: theme.spacing(2),
    marginBottom: theme.spacing(1),
    borderRadius: '10px',
    height: '50px',
  },
  select: {
    width: '100%',
  },
}));
  

function StartLog(props) {

  const classes = useStyles();
  const history = useHistory();

  const puzzleNameField = useRef();
  const puzzleMakerField = useRef();
  const maxPiecesField = useRef();
  const puzzleModeField = useRef();

  const [errorMsg, setErrorMsg] = useState({});


  const newLog = (e) => {

    e.preventDefault();

    let errors = {};

    if (!puzzleNameField.current.value) {
      errors = {...errors, puzzleName: 'Write the name of the puzzle'}
    }

    if (!maxPiecesField.current.value) {
        errors = {...errors, maxPieces: 'Enter max pieces'}
      }

    if (!puzzleModeField.current.value) {
      errors = {...errors, puzzleMode: 'Choose puzzle mode'}
    }

    if (Object.entries(errors).length > 0) {

      setErrorMsg({...errors});
    
    } else {
      console.log('Form filled with no errors');
      setErrorMsg({});

      let newPuzzleLog = {
        id: uuid(),
        puzzleName: puzzleNameField.current.value,
        maxPieces: maxPiecesField.current.value,
        puzzleMaker: puzzleMakerField.current.value,
        puzzleMode: puzzleModeField.current.value,
        logStartDate: new Date().getTime(), 
        logEndDate: new Date().getTime(),
        totalTime: 0,
        stars: 0,
        chartData: [],
      };

      // Luodaan apuarray vanhasta listasta + lisyksest ja lajitellaan se uusimmasta vanhimpaan
      let newPuzzleLogPush = [...props.puzzleLog, newPuzzleLog].sort((a, b) => b.logStartDate - a.logStartDate);
      console.log(newPuzzleLogPush);

      // Listn lajiteltu apuarray uudeksi listaksi
      props.setPuzzleLog(newPuzzleLogPush);

      props.handleGetMaxInput(maxPiecesField.current.value);
      //
      console.log('tiedot lhetetty, start ok ja open false');
/*       props.setStartOk(true); */
      props.handleStartOk();
/*       props.setOpen(false); */
      console.log(newPuzzleLog.id);
      console.log(props.startOk);

      history.push(`/counter/${newPuzzleLog.id}`); // ${newPuzzleLog.id} ?? /counter:id ??

    }
  }

  return (

    <Dialog open={props.open} onClose={props.handleClose}>

      <DialogContent>

      <Container maxWidth='md' className={classes.page}>
      <div className={classes.root}>

        <MuiPickersUtilsProvider utils={DateFnsUtils} locale={fi}>
          <form id='dialogForm' onSubmit={newLog}>

            <Container style={{width:'100%'}}>
              <Grid container spacing={1}>

                <Grid item xs={12}>
                    <Typography
                    variant='h5'
                    component='h1'
                    className={classes.header}
                    >Start a new puzzle log?
                    </Typography>
                </Grid>

              </Grid>
            </Container>

            <Container style={{ width:'100%' }}>
              <Grid container spacing={1}>

                <Grid item xs={12}>
                  <TextField
                  name='puzzleName'
                  variant='outlined'
                  label='Puzzle name *'
                  placeholder='Write the name of the puzzle'
                  InputLabelProps={{ shrink: true }}
                  inputRef={puzzleNameField}
                  className={classes.input}
                  error={Boolean(errorMsg.puzzleName)}
                  helperText={errorMsg.puzzleName}
                  />
                </Grid>

              </Grid>
            </Container>

            <Container style={{ width:'100%' }}>
              <Grid container spacing={1}>

                <Grid item xs={6}>
                  <TextField
                  name='maxPieces'
                  variant='outlined'
                  label='Max pieces *'
                  placeholder='Max pieces'
                  InputLabelProps={{ shrink: true }}
                  inputRef={maxPiecesField}
                  className={classes.input}
                  error={Boolean(errorMsg.maxPieces)}
                  helperText={errorMsg.maxPieces}
                  />
                </Grid>
              </Grid>
            </Container>

            <Container style={{ width:'100%' }}>
              <Grid container spacing={1}>

                <Grid item xs={10}>
                  <TextField
                  name='puzzleMaker'
                  variant='outlined'
                  label='Puzzle maker'
                  placeholder='(Write the puzzle maker)'
                  InputLabelProps={{ shrink: true }}
                  inputRef={puzzleMakerField}
                  className={classes.input}
                  error={Boolean(errorMsg.puzzleMaker)}
                  helperText={errorMsg.puzzleMaker}
                  />
                </Grid>

              </Grid>
            </Container>

            <Container style={{ width:'100%'}}>
              <Grid container spacing={1}>
                <Grid item xs={10}>

                <FormControl
                  variant='outlined'
                  className={classes.input}
                  >
                  <InputLabel
                    shrink={true}
                  >Puzzle mode *</InputLabel>
                  <Select
                    name='puzzleMode'
                    variant='outlined'
                    label='Puzzle mode *'
                    InputLabelProps={{ shrink: true }}
                    inputRef={puzzleModeField}
                    className={classes.input}
                    error={Boolean(errorMsg.puzzleMode)}
                    helperText={errorMsg.puzzleMode}
                  >
                    <option value='' disabled>Choose puzzle mode</option>
                    <option value='Solo casual'>Solo casual</option>
                    <option value='Solo competitive'>Solo competitive</option>
                    <option value='2-4 people casual'>2-4 people casual</option>
                    <option value='2-4 people competitive'>2-4 people competitive</option>
                  </Select>
                </FormControl>

                </Grid>
              </Grid>
            </Container>

          </form>
        </MuiPickersUtilsProvider>

      </div>
      </Container>

      </DialogContent>

      <DialogActions>
        <Button onClick={props.handleClose}>Cancel</Button>
        <Button type='submit' form='dialogForm' /* onClick={props.handleStart} */>Start new log</Button>
      </DialogActions> 

    </Dialog>
  );
}

export default StartLog;
