import { makeStyles } from '@material-ui/core';
import { Container, Grid, Typography
       } from '@material-ui/core';
import PuzzleList from './PuzzleList';

/* Styles for the Log page */

const useStyles = makeStyles( (theme) => ({

  root: {
    flexGrow: 1,
  },
  page: {
    fontFamily: "'Segoe UI', 'Roboto', 'Arial', 'Helvetica', sans-serif",
    textAlign: 'center',
    padding: '0',  
  },
  body: {
    padding: theme.spacing(2),
    marginTop: theme.spacing(0),
    marginBottom: theme.spacing(0),
  },
  header: {
    fontFamily: "'Segoe UI', sans-serif",
    fontWeight: 'bold',
    color: 'darkslateblue', 
    marginTop: theme.spacing(2),
    marginBottom: theme.spacing(1),
    padding: theme.spacing(1), 
  },
  subheader: {
    fontFamily: "'Segoe UI', sans-serif",
    fontWeight: 'bold',
    marginTop: theme.spacing(0),
    marginBottom: theme.spacing(0), 
  },
  formControl: {
    margin: theme.spacing(1),
    width: '100%',
    textAlign: 'left',
  },
  selectEmpty: {
    marginTop: theme.spacing(2),
  },
}));


function Log(props) {

  const classes = useStyles();

  return (

    <Container maxWidth='md' className={classes.page}>

      <div className={classes.root}>

        <Container style={{width:'100%'}}>
        <Grid container spacing={1}>

          <Grid item xs={12}>
            <Typography
              variant='h4'
              component='h1'
              className={classes.header}
            >Puzzle Log
            </Typography>
          </Grid>

        </Grid>
        </Container>

        <Container style={{width:'90%'}}>
        <Grid container spacing={1}>

            <PuzzleList 
              puzzleLog={props.puzzleLog} 
              setPuzzleLog={props.setPuzzleLog}
            />

        </Grid>
        </Container>

      </div>
    </Container>
  );
}

export default Log;