import { makeStyles } from '@material-ui/core';
import { Container, Grid,
         Typography, Button, 
      } from '@material-ui/core';
import { Link } from 'react-router-dom';
import ShowOnline from './Twitch/ShowOnline';
import ExtensionIcon from '@material-ui/icons/Extension';
import TrendingUpIcon from '@material-ui/icons/TrendingUp';
import LibraryBooksIcon from '@material-ui/icons/LibraryBooks';

/* Styles for Home page */

const useStyles = makeStyles( (theme) => ({

  root: {
    flexGrow: 1,
  },
  page: {
    fontFamily: "'Segoe UI', 'Roboto', 'Arial', 'Helvetica', sans-serif",
    textAlign: 'center',
    padding: '0',  
  },
  header: {
    fontFamily: "'Segoe UI', sans-serif",
    fontWeight: 'bold',
    color: 'darkslateblue', 
    marginTop: theme.spacing(3),
    marginBottom: theme.spacing(2),
    padding: theme.spacing(2), 
  },
  subheader: {
    fontFamily: "'Segoe UI', sans-serif",
    fontWeight: 'bold',
    marginTop: theme.spacing(2),
    marginBottom: theme.spacing(5), 
  },
  body: {
    padding: theme.spacing(2),
    marginTop: theme.spacing(0),
    marginBottom: theme.spacing(0),
  },
  buttonFull: {
    width: '100%',
    marginTop: theme.spacing(1),
    marginBottom: theme.spacing(1),
    backgroundColor: 'green', 
    color: 'white',
    fontWeight: 'bold',
    fontSize: 'medium',
    borderRadius: '10px',
    height: '100px',
  },
  button: {
    width: '100%',
    marginTop: theme.spacing(1),
    marginBottom: theme.spacing(1),
    backgroundColor: 'darkslateblue',
    borderRadius: '10px',
    height: '100px',
  },
  icon: {
    fontSize: '40px',
    marginRight: '10px',
  },
}));


function Home(props) {

  const classes = useStyles();

  return (

    <Container maxWidth='md' className={classes.page}>

      <div className={classes.root}>

        <Container style={{width:'100%'}}>
        <Grid container spacing={1}>

          <Grid item xs={12}>

            <Typography
              variant='h4'
              component='h1'
              className={classes.header}
            >Puzzle Stream Pal
            </Typography>

            <Typography
              variant='h6'
              component='h2'
              className={classes.subheader}
            >Jigsaw puzzle streaming made easier
            </Typography>

          </Grid>

        </Grid>
        </Container>

        <Container style={{width:'90%'}}>
          <Grid container spacing={1}>

            <Grid item xs={12}>
              <Button
                variant='contained'
                className={classes.buttonFull}
                component={Link}
                to='/counter/start'
                ><ExtensionIcon className={classes.icon} />
                  Start puzzle counter
              </Button>
            </Grid>

            <Grid item xs={6}>
                <Button
                    variant='contained'
                    color='primary'
                    className={classes.button}
                    component={Link}
                    to='/stats'
                    ><TrendingUpIcon className={classes.icon} />
                      Puzzle stats
                </Button>
            </Grid>

            <Grid item xs={6}>
                <Button
                    variant='contained'
                    color='primary'
                    className={classes.button}
                    component={Link}
                    to='/log'
                    ><LibraryBooksIcon className={classes.icon} />
                      Puzzle log
                </Button>
            </Grid>

          </Grid>
        </Container>

        <Container style={{width:'90%'}}>
          <Grid container spacing={2}>

            <Grid item xs={12}>
              <Typography
                variant='h5'
                component='h2'
                className={classes.header}
              >Online on Twitch
              </Typography>
            </Grid>

            <Grid item xs={12}>

                <ShowOnline />

            </Grid>

          </Grid>
        </Container>

      </div>
    </Container>
  );
}

export default Home;