import { useState } from 'react';
import { makeStyles } from '@material-ui/core';
import { Container } from "@material-ui/core";
import StarIcon from '@material-ui/icons/Star';
import StarBorderIcon from '@material-ui/icons/StarBorder';

/* Styles for the StarRating component  */

const useStyles = makeStyles( (theme) => ({

  root: {
    flexGrow: 1,
  },
  page: {
    fontFamily: "'Segoe UI', 'Roboto', 'Arial', 'Helvetica', sans-serif",
    textAlign: 'left',
    padding: '0',  
  },
  iconSmall: {
      color: 'darkslateblue',
      marginRight: theme.spacing(0),
      marginLeft: theme.spacing(0),
      marginTop: theme.spacing(0),
      marginBottom: theme.spacing(0),
      height: '16px',
  },
}));

function StarRating(props) {

  const classes = useStyles();

/*   const [rating, setRating] = useState();
  
  const HandleRating = () => {
      setRating(props.puzzleLog.stars);
  } */
  const rating = props.stars;
  console.log(rating);
  
  return (

    <Container maxWidth='md' className={classes.page}>
      <div className={classes.root}>
        
        {(rating == 5) 
            ? <><StarIcon className={classes.iconSmall} />
                <StarIcon className={classes.iconSmall} />
                <StarIcon className={classes.iconSmall} />
                <StarIcon className={classes.iconSmall} />
                <StarIcon className={classes.iconSmall} />
             </>
            : (rating == 4)
                ? <>
                    <StarIcon className={classes.iconSmall} />
                    <StarIcon className={classes.iconSmall} />
                    <StarIcon className={classes.iconSmall} />
                    <StarIcon className={classes.iconSmall} />
                    <StarBorderIcon className={classes.iconSmall} />
                  </>
                : (rating == 3) 
                    ? <>
                        <StarIcon className={classes.iconSmall} />
                        <StarIcon className={classes.iconSmall} />
                        <StarIcon className={classes.iconSmall} />
                        <StarBorderIcon className={classes.iconSmall} />
                        <StarBorderIcon className={classes.iconSmall} />
                      </>
                    : (rating == 2)
                        ? <>
                            <StarIcon className={classes.iconSmall} />
                            <StarIcon className={classes.iconSmall} />
                            <StarBorderIcon className={classes.iconSmall} />
                            <StarBorderIcon className={classes.iconSmall} />
                            <StarBorderIcon className={classes.iconSmall} />
                          </>
                        : (rating == 1)
                            ? <>
                                <StarIcon className={classes.iconSmall} />
                                <StarBorderIcon className={classes.iconSmall} />
                                <StarBorderIcon className={classes.iconSmall} />
                                <StarBorderIcon className={classes.iconSmall} />
                                <StarBorderIcon className={classes.iconSmall} />
                              </>
                            : (rating == 0)
                                ? <>
                                    <StarBorderIcon className={classes.iconSmall} />
                                    <StarBorderIcon className={classes.iconSmall} />
                                    <StarBorderIcon className={classes.iconSmall} />
                                    <StarBorderIcon className={classes.iconSmall} />
                                    <StarBorderIcon className={classes.iconSmall} />
                                  </>
                                : <>
                                    <StarBorderIcon className={classes.iconSmall} />
                                    <StarBorderIcon className={classes.iconSmall} />
                                    <StarBorderIcon className={classes.iconSmall} />
                                    <StarBorderIcon className={classes.iconSmall} />
                                    <StarBorderIcon className={classes.iconSmall} />
                                  </>
        }
      </div>
    </Container>
  );
}

export default StarRating;