import { useState, useEffect } from 'react';
import { makeStyles } from '@material-ui/core';
import { Container, Grid, Typography,
          FormControl, Select, } from '@material-ui/core';
import PuzzleChart from './PuzzleChart';
import PuzzleStats from './PuzzleStats';

/* Styles for the Stats page */

const useStyles = makeStyles( (theme) => ({

  root: {
    flexGrow: 1,
  },
  page: {
    fontFamily: "'Segoe UI', 'Roboto', 'Arial', 'Helvetica', sans-serif",
    textAlign: 'center',
    padding: '0',  
  },
  body: {
    padding: theme.spacing(2),
    marginTop: theme.spacing(0),
    marginBottom: theme.spacing(0),
  },
  header: {
    fontFamily: "'Segoe UI', sans-serif",
    fontWeight: 'bold',
    color: 'darkslateblue', 
    marginTop: theme.spacing(2),
    marginBottom: theme.spacing(1),
    padding: theme.spacing(1), 
  },
  formControl: {
    margin: theme.spacing(1),
    width: '100%',
    textAlign: 'left',
  },
  input: {
    width: '100%',
    marginTop: theme.spacing(0),
    marginBottom: theme.spacing(2),
    textAlign: 'left',
    height: '50px',
},
}));


function Stats(props) {

  const classes = useStyles();

  const [statsId, setStatsId] = useState();

  const handleChange = (event) => {
    setStatsId(event.target.value);
  };

  useEffect( () => {
    console.log(statsId);
  }, [statsId])


  return (

    <Container maxWidth='md' className={classes.page}>

      <div className={classes.root}>

        <Container style={{width:'100%'}}>
        <Grid container spacing={1}>

          <Grid item xs={12}>
            <Typography
              variant='h4'
              component='h1'
              className={classes.header}
            >Puzzle stats
            </Typography>
          </Grid>

        </Grid>
        </Container>

        <Container style={{width:'90%'}}>
        <Grid container spacing={1}>

            <Grid item xs={12}>
            <FormControl 
                variant="outlined" 
                className={classes.input}
            >
                    <Select
                        defaultValue={props.puzzleLog[0].id}
                        displayEmpty
                        onChange={handleChange}
                        className={classes.input}
                        style={{borderRadius: '8px'}}
                    >
                      {props.puzzleLog.map( (puzzleLog) => 
                        <option key={puzzleLog.id} value={puzzleLog.id}>{puzzleLog.puzzleName}</option>
                      )}
                    </Select>
            </FormControl>
            </Grid>

        </Grid>
        </Container>

        <Container style={{width:'100%'}}>
        <Grid container spacing={1}>

          <PuzzleChart 
            puzzleLog={props.puzzleLog}
            setPuzzleLog={props.setPuzzleLog}
            statsId={statsId}
          />

          <PuzzleStats 
            puzzleLog={props.puzzleLog}
            setPuzzleLog={props.setPuzzleLog}
            statsId={statsId}
          />

        </Grid>
        </Container>

      </div>
    </Container>
  );
}

export default Stats;