
import { useState, useEffect } from 'react';
import { makeStyles } from '@material-ui/core';
import { Container, Grid,
         Typography, Button, 
      } from '@material-ui/core';
import { useParams } from 'react-router-dom';
import { useHistory } from 'react-router-dom';
import AddCircleIcon from '@material-ui/icons/AddCircle';
import RemoveCircleIcon from '@material-ui/icons/RemoveCircle';

/* Styles for the PieceCounter component */

const useStyles = makeStyles( (theme) => ({

  root: {
    flexGrow: 1,
  },
  page: {
    fontFamily: "'Segoe UI', 'Roboto', 'Arial', 'Helvetica', sans-serif",
    textAlign: 'center',
    padding: '0',  
  },
  header: {
    fontFamily: "'Segoe UI', sans-serif",
    fontWeight: 'bold',
    color: 'darkslateblue', 
    marginTop: theme.spacing(0),
    marginBottom: theme.spacing(1),
    padding: theme.spacing(1), 
  },
  subheader: {
    fontFamily: "'Segoe UI', sans-serif",
    fontWeight: 'bold',
    marginTop: theme.spacing(0),
    marginBottom: theme.spacing(0), 
  },
  body: {
    padding: theme.spacing(2),
    marginTop: theme.spacing(0),
    marginBottom: theme.spacing(0),
  },
  reset: {
      textAlign: 'right',
      marginBottom: '0',
  },
  buttonCounterAdd: {
    width: '100%',
    marginTop: theme.spacing(1),
    marginBottom: theme.spacing(1),
    backgroundColor: 'darkslateblue',
    borderRadius: '10px',
    height: '250px',
  },
  buttonCounterRemove: {
    width: '100%',
    marginTop: theme.spacing(1),
    marginBottom: theme.spacing(1),
    backgroundColor: '#918ab9',
    borderRadius: '10px',
    height: '150px',
  },
  iconBig: {
      fontSize: '100px',
  },
}));


function PieceCounter(props) {

  const classes = useStyles();
  const history = useHistory();
  const {id} = useParams();

  const currentLog = props.puzzleLog.filter((puzzleLog) => {
    return (puzzleLog.id === id);
  })[0];

  const [maxPieces, setMaxPieces] = useState(1000);

  const maxInput = () => {
    if(currentLog) {
      setMaxPieces(currentLog.maxPieces);
    }
  };

  const [pieces, setPieces] = useState(0);

  const addPieces = () => {
    if (pieces <= currentLog.maxPieces) {
      setPieces(pieces => pieces + 1);
    }
  };

  const removePieces = () => {
    if (pieces > 0 ) {
      setPieces(pieces => pieces -1);
    }
  };

  const updateLog = () => {

    if (currentLog != null) {
      
      let newData = {
        pieces: pieces, // palojen mr napin painamishetkell
        time: new Date().getTime(), // napin painamisaika
      };

      console.log(currentLog.chartData);
      console.log(newData);

      let newChart = [...currentLog.chartData, newData];
      let newTotal = Math.floor(new Date().getTime() - currentLog.logStartDate);
      console.log(newTotal);

      let newChartData = {
        id: id,
        puzzleName: currentLog.puzzleName,
        maxPieces: currentLog.maxPieces,
        puzzleMaker: currentLog.puzzleMaker,
        puzzleMode: currentLog.puzzleMode,
        logStartDate: currentLog.logStartDate,
        logEndDate: new Date().getTime(),
        totalTime: newTotal,
        chartData: newChart,
      };

      console.log(newChartData.totalTime);
      // Otetaan muut kuin tt id:t vastaavat vanhat logit talteen
      let oldPuzzleLog = props.puzzleLog.filter( (puzzleLog) => {
          return(puzzleLog.id !== id);
      });

      // Luodaan datan pushaamiseksi apuarray vanhasta listasta + lisyksest
      let newDataPush = [...oldPuzzleLog, newChartData];
      console.log(newDataPush);

      // Listn apuarray uudeksi listaksi vanhan tilalle
      props.setPuzzleLog(newDataPush);

    }
  }

  console.log(pieces);
  console.log(id);
  console.log(currentLog);

  const resetPieces = () => {
    setPieces(0);
  };

  useEffect( () => {
    resetPieces();
  }, [props.getReset])

  useEffect( () => {
    maxInput();
  }, [props.getMaxInput])

  useEffect( () => {
    updateLog();
  }, [pieces]);

  return (

    <Container maxWidth='md' className={classes.page}>

      <div className={classes.root}>

        <Container style={{width:'100%'}}>
            <p className={classes.reset} onClick={resetPieces}>Reset counter</p>
        </Container>
        
        <Container style={{width:'100%'}}>
            <Grid container spacing={1}>

                <Grid item xs={12}>
                    <Typography
                    variant='h5'
                    component='h2'
                    className={classes.subheader}
                    >Piece counter
                    </Typography>
                </Grid>

                <Grid item xs={12}>
                    <Typography
                    variant='h4'
                    component='h3'
                    className={classes.header}
                    >{pieces} / {maxPieces}
                    </Typography>
                </Grid>

            </Grid>
        </Container>

        <Container style={{width:'90%'}}>
            <Grid container spacing={1}>

                <Grid item xs={12}>
                    <Button
                        variant='contained'
                        color='primary'
                        className={classes.buttonCounterRemove}
                        style={{ backgroundColor: '#918ab9'}}
                        onClick={removePieces}
                        disabled={props.getPaused}
                        ><RemoveCircleIcon className={classes.iconBig}/>
                    </Button>
                </Grid>

            </Grid>

            <Grid container spacing={1}>

                <Grid item xs={12}>
                    <Button
                        variant='contained'
                        color='primary'
                        className={classes.buttonCounterAdd}
                        style={{ backgroundColor: 'darkslateblue'}}
                        onClick={addPieces}
                        disabled={props.getPaused}
                        ><AddCircleIcon className={classes.iconBig}/>
                    </Button>
                </Grid>

            </Grid>
        </Container>

      </div>
    </Container>
  );
}

export default PieceCounter;
