import { makeStyles } from '@material-ui/core';
import { ListItem, List, ListItemText, 
          Grid, Container, Divider, 
          Typography, Button, 
        } from "@material-ui/core";
import ExtensionIcon from '@material-ui/icons/Extension';
import AccountCircleIcon from '@material-ui/icons/AccountCircle';
import HourglassEmptyIcon from '@material-ui/icons/HourglassEmpty';
import VisibilityIcon from '@material-ui/icons/Visibility';

/* Styles for the ShowOnline component  */

const useStyles = makeStyles( (theme) => ({

  root: {
    flexGrow: 1,
  },
  page: {
    fontFamily: "'Segoe UI', 'Roboto', 'Arial', 'Helvetica', sans-serif",
    textAlign: 'center',
    padding: '0',  
  },
  body: {
    padding: theme.spacing(2),
    marginTop: theme.spacing(0),
    marginBottom: theme.spacing(0),
  },
  subheader: {
    fontFamily: "'Segoe UI', sans-serif",
    fontWeight: 'bold',
    marginTop: theme.spacing(3),
    marginBottom: theme.spacing(5), 
  },
  iconSmall: {
    color: 'darkslateblue',
    marginRight: theme.spacing(1),
    marginLeft: theme.spacing(4),
    marginTop: theme.spacing(1),
    height: '16px',
},
iconSmallFirst: {
  color: 'darkslateblue',
  marginRight: theme.spacing(1),
  marginLeft: theme.spacing(0),
  marginTop: theme.spacing(1),
  height: '16px',
},
list: {
  width: '100%',
  marginTop: theme.spacing(1),
  marginBottom: theme.spacing(2),
},
listItem: {
  border: '2px',
},
button: {
  width: '100%',
  marginTop: theme.spacing(1),
  marginBottom: theme.spacing(1),
  backgroundColor: 'darkslateblue',
  color: 'white',
  fontWeight: 'bold',
  borderRadius: '10px',
  height: '50px',
},
links: {
  textDecoration: 'none',
},
}));


function ShowOnline(props) {

  const classes = useStyles();

  const text = {
    fontWeight: 'bold',
    marginBottom: '15px',
  }

  const smalltext = {
    fontSize: '14px',
    marginRight: '10px',
    //minWidth: '40px',
  }

  return (

    <Container maxWidth='md' className={classes.page}>
      <div className={classes.root}>

      {(props.streams == null)
        ? <>
            <Container style={{width:'100%'}}>
              <Grid container spacing={1} style={{justifyContent:'center'}}>
              <Grid item xs={12}>
                <Typography
                  variant='h6'
                  component='h2'
                  className={classes.subheader}
                >No channels online at the moment
                </Typography>
              </Grid>
              <Grid item xs={6}>
                  <Button
                      variant='contained'
                      color='default'
                      className={classes.button}
                      onClick={props.handleRefresh}
                      >Refresh
                  </Button>
              </Grid>
              </Grid>
            </Container>
          </>
        : <>
          <Container style={{width:'100%'}}>
            <Grid container spacing={1}>

              <Grid item xs={12}>
                <List className={classes.list} disablePadding>
                  <Divider/>

                    {props.streams.map((stream, idx) => {

                      return (

                        <ListItem 
                            key={idx} 
                            className={classes.ListItem}
                            divider={true}
                        >
                          <ListItemText 
                              primaryTypographyProps={{ style: text }}
                              secondaryTypographyProps={{ style: smalltext }}
                              primary={<>
                                      <ExtensionIcon className={classes.iconSmallFirst} />
                                      {stream.title}
                                      </>}
                              secondary={<>                                
                                  <AccountCircleIcon className={classes.iconSmallFirst} />
                                  {stream.user_name} 
                                  <VisibilityIcon className={classes.iconSmall} />
                                  {stream.viewer_count} {(stream.viewer_count==1) ? 'viewer' : 'viewers'}
                                  <br/>
                                  <HourglassEmptyIcon className={classes.iconSmallFirst} />
                                  {stream.started_at}
                              </>}
                          />
                        </ListItem>
                      );

                    })}
                  
                </List>
              </Grid>

            </Grid>
          </Container>

          <Container style={{ width: '85%'}}>
            <Grid container spacing={1} style={{justifyContent: 'center'}}>

              <Grid item xs={10}>
                  <a href='https://www.twitch.tv/directory/game/Jigsaw%20Puzzle'
                      target='_blank'
                      rel='noopener noreferrer'
                      className={classes.links}
                  >
                  <Button
                      variant='contained'
                      color='default'
                      className={classes.button}
                      >Go to Twitch
                  </Button></a>
              </Grid>
              
            </Grid>
          </Container>
          </>
        }

      </div>
    </Container>
  );
}

export default ShowOnline;
