
import { makeStyles } from '@material-ui/core';
import { Container, Grid,
         Typography, Button, 
      } from '@material-ui/core';

/* Styles for the Timer component  */

const useStyles = makeStyles( (theme) => ({

  root: {
    flexGrow: 1,
  },
  page: {
    fontFamily: "'Segoe UI', 'Roboto', 'Arial', 'Helvetica', sans-serif",
    textAlign: 'center',
    padding: '0',  
  },
  timer: {
    fontFamily: "'Segoe UI', sans-serif",
    fontSize: '3rem',
    fontWeight: 'bold',
    color: 'darkslateblue', 
    marginTop: theme.spacing(0),
    marginBottom: theme.spacing(1),
    padding: theme.spacing(0.5), 
  },
  subheader: {
    fontFamily: "'Segoe UI', sans-serif",
    fontWeight: 'bold',
    marginTop: theme.spacing(2),
    marginBottom: theme.spacing(0), 
  },
}));


function Timer(props) {

  const classes = useStyles();

  return (

    <Container maxWidth='md' className={classes.page}>

      <div className={classes.root}>

        <Container style={{width:'100%'}}>
            <Grid container spacing={1}>

            <Grid item xs={12}>
                <Typography
                variant='h5'
                component='h1'
                className={classes.subheader}
                >Timer
                </Typography>
            </Grid>

            <Grid item xs={12}>
                <Typography>
                
                    <span id='hours' className={classes.timer}>
                        {("0" + Math.floor((props.time / 360000) % 60)).slice(-2)}
                    </span>
                    <span className={classes.timer}>:</span>
                    <span id='minutes' className={classes.timer}>
                        {("0" + Math.floor((props.time / 60000) % 60)).slice(-2)}
                    </span>
                    <span className={classes.timer}>:</span>
                    <span id='seconds' className={classes.timer}>
                        {("0" + Math.floor((props.time / 1000) % 60)).slice(-2)}
                    </span>

                </Typography>
            </Grid>

            </Grid>
        </Container>

      </div>
    </Container>
  );
}

export default Timer;
