
import { makeStyles } from '@material-ui/core';
import { Container, Grid, Button,
      } from '@material-ui/core';
import StartLog from './StartLog';

/* Styles for the TimerButtons component  */

const useStyles = makeStyles( (theme) => ({

  root: {
    flexGrow: 1,
  },
  page: {
    fontFamily: "'Segoe UI', 'Roboto', 'Arial', 'Helvetica', sans-serif",
    textAlign: 'center',
    padding: '0',  
  },
  buttonTimer: {
    width: '100%',
    marginTop: theme.spacing(1),
    marginBottom: theme.spacing(2),
    backgroundColor: 'darkslateblue',
    borderRadius: '10px',
    height: '50px',
  },
  buttonTimerReset: {
    width: '100%',
    marginTop: theme.spacing(1),
    marginBottom: theme.spacing(2),
    backgroundColor: '#918ab9',
    borderRadius: '10px',
    height: '50px',
  },
  buttonTimerResume: {
    width: '100%',
    marginTop: theme.spacing(1),
    marginBottom: theme.spacing(2),
    backgroundColor: 'darkslateblue',
    borderRadius: '10px',
    height: '50px',
  },
}));


function TimerButtons(props) {

  const classes = useStyles();

  const StartButton = (
    <Grid item xs={12}>
        <Button
            variant='contained'
            color='primary'
            className={classes.buttonTimer}
            style={{ backgroundColor: 'green'}}
            onClick={props.handleClickOpen}
            >Start
        </Button>

        <StartLog 
          open={props.open}
          setOpen={props.setOpen}
          handleClose={props.handleClose}
          handleStartOk={props.handleStartOk}
          handleClickOpen={props.handleClickOpen}
          handleGetMaxInput={props.handleGetMaxInput}
          setStartOk={props.setStartOk}
          startOk={props.startOk}
          puzzleLog={props.puzzleLog}
          setPuzzleLog={props.setPuzzleLog}
        />
    </Grid>
  );

  const ActiveButtons = (
    <>
    <Grid item xs={6}>
        <Button
            variant='contained'
            color='primary'
            className={classes.buttonTimer}
            style={{ backgroundColor: '#918ab9'}}
            onClick={props.handleReset}
            >Reset
        </Button>
    </Grid>

    <Grid item xs={6}>
        {props.isPaused 
            ? 
                <Button
                    variant='contained'
                    color='primary'
                    className={classes.buttonTimer}
                    style={{ backgroundColor: 'green'}}
                    onClick={props.handlePauseResume}
                    >Resume
                </Button>
            :   
                <Button
                    variant='contained'
                    color='primary'
                    className={classes.buttonTimer}
                    style={{ backgroundColor: 'red'}}
                    onClick={props.handlePauseResume}
                    >Pause
                </Button>
        }
    </Grid>
    </>
  );

  return (

    <Container maxWidth='md' className={classes.page}>

      <div className={classes.root}>

        <Container style={{width:'90%'}}>
            <Grid container spacing={1}>

                {props.isActive
                    ? ActiveButtons
                    : StartButton
                }

            </Grid>
        </Container>

      </div>
    </Container>
  );
}

export default TimerButtons;
