import { makeStyles } from '@material-ui/core';
import { Container, Typography, Button } from "@material-ui/core";
import { Link, useHistory, useParams } from 'react-router-dom';

/* Styles for Delete page */

const useStyles = makeStyles( (theme) => ({

    root: {
      flexGrow: 1,
    },
    page: {
      fontFamily: "'Segoe UI', 'Roboto', 'Arial', 'Helvetica', sans-serif",
      textAlign: 'center',
      padding: '0',  
    },
    subheader: {
      fontFamily: "'Segoe UI', sans-serif",
      fontWeight: 'bold',
      marginTop: theme.spacing(5),
      marginBottom: theme.spacing(5), 
    },
    button: {
      width: '50%',
      marginTop: theme.spacing(1),
      marginBottom: theme.spacing(1),
      marginRight: theme.spacing(1),
      marginLeft: theme.spacing(1),
      backgroundColor: 'darkslateblue',
      borderRadius: '10px',
      height: '50px',
    },
    buttonCancel: {
        width: '50%',
        marginTop: theme.spacing(1),
        marginBottom: theme.spacing(1),
        marginRight: theme.spacing(1),
        marginLeft: theme.spacing(1),
        borderRadius: '10px',
        height: '50px',
      },
  }));
  

function Delete(props) {

    const classes = useStyles();
    const history = useHistory();
    const { id } = useParams();

    const logToDelete = props.puzzleLog.filter((puzzleLog) => {
                                return (puzzleLog.id === id);
                            })[0];

    const deleteLog = (e) => {

        e.preventDefault();

        let deleteLogPush = props.puzzleLog.filter( (puzzleLog) => {
            return (puzzleLog.id !== id);
        });

        props.setPuzzleLog([...deleteLogPush]);

        history.push('/log');
    }

    return (

        <Container maxWidth='md' className={classes.page}>

            <Typography
              variant='h5'
              component='h1'
              className={classes.subheader}
            >Delete the puzzle log for <br/> 
            <span style={{ color: 'darkslateblue' }}> 
                {logToDelete.puzzleName}
            </span> ?
            </Typography>

            <form onSubmit={deleteLog}>

            <Button
                type='submit'
                variant='contained'
                color='primary'
                className={classes.button}
            >Delete log
            </Button>

            <Button
                variant='contained'
                color='default'
                className={classes.buttonCancel}
                component={Link}
                to='/log'
                exact='true'
            >Cancel
            </Button>

            </form>
        </Container>
    );
}

export default Delete;
