import React from 'react';
import { useState } from 'react';
import { ListItemIcon, makeStyles, Typography } from '@material-ui/core';
import { AppBar, Toolbar, 
        IconButton, Drawer,
        List, ListItemText, 
        ListItem, CssBaseline,
    } from '@material-ui/core';
import { Link } from 'react-router-dom';
import MenuIcon from '@material-ui/icons/Menu';
import HomeIcon from '@material-ui/icons/Home';
import LibraryBooks from '@material-ui/icons/LibraryBooks';
import ExtensionIcon from '@material-ui/icons/Extension';
import TrendingUpIcon from '@material-ui/icons/TrendingUp';
import LiveTvIcon from '@material-ui/icons/LiveTv';


/* Styles for the left-side menu and top appbar */

const useStyles = makeStyles( (theme) => ({

  appbar: {
    flex: '1 1 auto',
    backgroundColor: 'darkslateblue',
  },
  title: {
    flexGrow: 1,
    display: 'block',
    textAlign: 'center',
    textTransform: 'uppercase',
    fontWeight: 'bold',
    fontSize: 'medium',
  },
  icon: {
    fontSize: '16px',
    marginRight: '10px',
  },
}));

function Menu() {

    const classes = useStyles();

    const [naytaValikko, setNaytaValikko] = useState(false);
    
  return (

    <CssBaseline>
        <div>
            <AppBar
                position='static' 
                className={classes.appbar}
            >
                <Toolbar>

                    <IconButton
                        edge='start'
                        color='inherit'
                        onClick={ () => {
                            setNaytaValikko(true)
                        }}
                    >
                        <MenuIcon />
                    </IconButton>

                    <Typography 
                        variant='h6'
                        className={classes.title}
                        noWrap
                    ><ExtensionIcon className={classes.icon} /> Puzzle stream pal
                    </Typography>

                    <Drawer
                        open={naytaValikko}
                        onClose={ () => {
                            setNaytaValikko(false)
                        }}
                    >
                        <List 
                            style={{
                                width: 225, // 200
                                marginTop: 10, // 50
                            }}
                                onClick={ () => { 
                                    setNaytaValikko(false)
                                }}   
                        >
                            <ListItem 
                                button 
                                component={Link}
                                to='/'exact>
                                <ListItemIcon>
                                    <HomeIcon />
                                </ListItemIcon>
                                <ListItemText primary='Home' />
                            </ListItem>

                            <ListItem 
                                button 
                                component={Link}
                                to='/counter/start'>
                                <ListItemIcon>
                                    <ExtensionIcon />
                                </ListItemIcon>
                                <ListItemText primary='Puzzle Counter' />
                            </ListItem>

                            <ListItem 
                                button 
                                component={Link}
                                to='/stats'>
                                <ListItemIcon>
                                    <TrendingUpIcon />
                                </ListItemIcon>
                                <ListItemText primary='Stats' />
                            </ListItem>

                            <ListItem 
                                button 
                                component={Link}
                                to='/log'>
                                <ListItemIcon>
                                    <LibraryBooks />
                                </ListItemIcon>
                                <ListItemText primary='Puzzle Log' />
                            </ListItem>

                            <ListItem 
                                button 
                                component={Link}
                                to='/twitch'>
                                <ListItemIcon>
                                    <LiveTvIcon />
                                </ListItemIcon>
                                <ListItemText primary='Twitch' />
                            </ListItem>

                        </List>
                    </Drawer>

                </Toolbar>
            </AppBar>
        </div>
    </CssBaseline>

  );
}

export default Menu;